<template>
  <v-app class="login-container">
    <v-form ref="loginForm" v-model="valid" class="login-form">
      <v-row class="title-container">
        <v-col class="title" cols="12">
          登 录
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-text-field
            v-model="form.username"
            :rules="rules.username"
            label="用户名"
            autocomplete="off"
            required
          />
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-text-field
            v-model="form.password"
            autocomplete="off"
            :rules="rules.password"
            label="密码"
            required
          />
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-btn
            :loading="loading"
            :disabled="loading"
            block
            color="blue"
            class="white--text"
            @click="handleLogin"
          >
            登录
          </v-btn>
        </v-col>
      </v-row>
    </v-form>
  </v-app>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      loading: false,
      valid: false,
      form: {
        username: 'admin',
        password: 'admin'
      },
      rules: {
        username: [
          v => !!v || '请输入用户名',
          v => v.length <= 16 || '用户名长宽不能超过16位'
        ],
        password: [
          v => !!v || '请输入密码',
          v => v.length <= 16 || '密码长度不能超过16位长度'
        ]
      },
      redirect: undefined,
      otherQuery: {},
      msg: ''
    }
  },
  methods: {
    handleLogin() {
      this.$refs.loginForm.validate()
      if (this.valid) {
        this.loading = true
        this.$store.dispatch('user/login', { username: this.form.username,
          password: this.form.password }).then(() => {
          this.loading = false
          this.$router.push({ path: this.redirect || '/', query: this.otherQuery })
        }).catch(e => {
          console.error(e)
          this.msg = e
          console.log(this.msg)
          this.loading = false
        })
      }
    }
  }
}
</script>

